import common from '../../common/common.js'
export default {
	data() {
		return {
			call: true,
			monitor:{},
			name:'',
			mobile:'',
		}
	},
	onLoad() {
		this.getDays();
		this.name=uni.getStorageSync('data');
		this.mobile=uni.getStorageSync('mobile');
	},
	methods: {
		// 获取监控天数
		getDays() {
			var that=this;
			var data={};
			common.getData('muqiang/invitation/getData', data, (res) => {
				that.monitor=res;
			})
		},
		// 处理记录
		dispose() {
			uni.navigateTo({
				url: '../dispose/dispose'
			})
		},
		// 设置
		set() {
			uni.navigateTo({
				url: '../setInfo/setInfo'
			})
		},
		// 关于我们
		myInfo() {
			uni.navigateTo({
				url: '../regards/regards'
			})
		},
		// 开启推送
		pullOn() {
			var call = this.call;
			var that = this;
			if (call == true) {
				uni.showModal({
					title: '提示',
					content: '是否关闭接收预警',
					success: function(res) {
						if (res.confirm) {
							that.offPull();
							that.call = false;
						}
					}
				})
			} else if (call == false) {
				uni.showModal({
					title: '提示',
					content: '是否打开接收预警',
					success: function(res) {
						if (res.confirm) {
							that.pull();
							that.call = true;
						}
					}
				})
			}
		},
		pull() {
			uni.getProvider({
				service: 'push',
				success: function(res) {
					console.log(res.provider)
					// 个推的名称为 igexin
					if (~res.provider.indexOf('igexin')) {
						uni.subscribePush({
							provider: 'igexin',
							success: function(res) {
								console.log(res)
								console.log('success:' + JSON.stringify(res));
							}
						});
					}
				}
			});
		},
		// 关闭推送
		offPull() {
			uni.unsubscribePush({
				provider: 'igexin',
				success: function(res) {
					console.log(res)
					console.log('success:' + JSON.stringify(res));
				}
			});
		}
	}
}
